import React, { useRef, useState } from 'react';
import { Link } from 'react-scroll';
import hamburgerMenu from '../../assets/images/hamburgerMenu.WebP'
import styles from './Navbar.module.css';

const Navbar = ({ toggleTheme }) => {

    const [hamburgerToggle, setHamburgerToggle] = useState(false);
    const [prevScrollpos, setPrevScrollpos] = useState(window.pageYOffset);
    const currentScrollPos = window.pageYOffset;

    const navRef = useRef();

    const toggleHamburger = () => {
        setHamburgerToggle(!hamburgerToggle);
    }

    window.onscroll = function () {
        if (prevScrollpos > currentScrollPos) {
            navRef.current.style.top = '0';
        } else {
            navRef.current.style.top = '-110px';
        }
        setPrevScrollpos(currentScrollPos);
    }


    return (
        <nav ref={navRef}>
            <Link className={styles.logo} to='/home' spy={true} smooth={true}>TigranMets </Link>
            <div className={`${styles.menuItems} ${hamburgerToggle && styles.hamburgerActiveMenuItems}`}>
                <Link to='skills' spy={true} smooth={true} >Skills</Link>
                <Link to='przofilciency' spy={true} smooth={true}>Proficiency</Link>
                <Link to='experiences' spy={true} smooth={true}>Work Experiences</Link>
                <Link to='projects' spy={true} smooth={true}>Projects</Link>
                <Link to='achievements' spy={true} smooth={true}>Achievements</Link>
                <Link to='blogs' spy={true} smooth={true}>Blogs</Link>
                <Link to='contact_me' spy={true} smhooth={true}>Contact Me</Link>
                <div className={styles.themeToggle}>
                    <input id='toggle' className={styles.toggleControl} type='checkbox' />
                    <label for='toggle' onClick={toggleTheme}></label>
                </div>
            </div>
            <img className={styles.hamburgerMenu} src={hamburgerMenu} alt='hamburger menu' onClick={toggleHamburger} />
        </nav>
    );
}

export default Navbar;